	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Berlin Postponed","date":"09 Apr 2005","slug":"/2005-04-09-berlin-postponed/","tags":"Y2005"},"html":"<p>We have to postpone our Berlin weekend. We both have a case of stomach flu, and decided it would be best to wait until we, and the weather, were somewhat better.</p>\n<p>That means we won't be able to meet <a href=\"http://www.schockwellenreiter.de/\">Jörg and Gabi</a> as we had planned. Hopefully we can arrange a meeting next time.</p>"},"next":{"fields":{"title":"Bruce Springsteen in Hamburg","date":"07 Apr 2005","slug":"/2005-04-07-bruce-springsteen-in-hamburg/"}},"previous":{"fields":{"title":"Service with a Smile","date":"09 Apr 2005","slug":"/2005-04-09-service-with-a-smile/"}}},{"node":{"fields":{"title":"Bruce Springsteen in Hamburg","date":"07 Apr 2005","slug":"/2005-04-07-bruce-springsteen-in-hamburg/","tags":"Y2005"},"html":"<p><a title=\"Ringfahndung Blog: BRUCE SPRINGSTEEN fünfmal solo & live in Deutschland.\" href=\"http://www.ringfahndung.de/archives/2005/04/bruce_springste.html#more\">Ringfahndung Blog: BRUCE SPRINGSTEEN 5 solo concerts in Germany</a> Hamburg, 11 June, Color Line Arena. Also Berlin, Munich, Frankfurt, Düsseldorf</p>"},"next":{"fields":{"title":"All Pope, All The Time","date":"07 Apr 2005","slug":"/2005-04-07-all-pope-all-the-time/"}},"previous":{"fields":{"title":"Berlin Postponed","date":"09 Apr 2005","slug":"/2005-04-09-berlin-postponed/"}}},{"node":{"fields":{"title":"All Pope, All The Time","date":"07 Apr 2005","slug":"/2005-04-07-all-pope-all-the-time/","tags":"Y2005"},"html":"<p>Just trying to cash in on the media craze... PapalScott!</p>\n<p><a href=\"http://www.papascott.de/fotos/papalscott_shot.php\" onclick=\"window.open('http://www.papascott.de/fotos/papalscott_shot.php','popup','width=818,height=634,scrollbars=no,resizable=no,toolbar=no,directories=no,location=no,menubar=no,status=no,left=0,top=0'); return false\"><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2005/04/papalscott_shot-thumb.png\" width=\"320\" height=\"248\" border=\"0\" alt=\"papalscott\" /></a></p>\n<p><strong>Update:</strong> I got tired of the design after about 5 minutes, so the screenshot will have to do. I still think the <a href=\"http://www.spiegel.de/politik/ausland/0,1518,268142,00.html\">photo</a> (from Spiegel Online) with the papal light sabre is cool, though.</p>"},"next":{"fields":{"title":"Priority clash","date":"06 Apr 2005","slug":"/2005-04-06-priority-clash/"}},"previous":{"fields":{"title":"Bruce Springsteen in Hamburg","date":"07 Apr 2005","slug":"/2005-04-07-bruce-springsteen-in-hamburg/"}}}],"pathPrefix":"","first":false,"last":false,"index":325,"pageCount":1101}}

